<template>
    <div class="task-component-operation">
        <p v-if="error !== ''" class="error">{{error}}</p>
        <button @click="$emit('editbutton', 0)" class="close-button">–ó–∞–∫—Ä–∏—Ç–∏</button>
        <form @submit.prevent="editTask">
            <div>
                <label for="title">–ù–∞–∑–≤–∞ –∑–∞–≤–¥–∞–Ω–Ω—è</label>
                <input type="text" id="title" maxlength="256" v-model="title" required />
            </div>
            <div>
                <label for="description">–û–ø–∏—Å –∑–∞–≤–¥–∞–Ω–Ω—è</label>
                <textarea id="description" v-model="description" required></textarea>
            </div>
            <div>
                <label for="date">–î–∞—Ç–∞ –≤–∏–∫–æ–Ω–∞–Ω–Ω—è</label>
                <input type="date" id="date" v-model="date" :min="todayDate" required/>
            </div>
            <button type="submit">–ó–±–µ—Ä–µ–≥—Ç–∏ –∑–º—ñ–Ω–∏</button>
        </form>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'EditTask',
    props: {
        task: Object
    },
    data() {
        return {
            title: this.task.title || '',
            description: this.task.description || '',
            completed: this.task.completed,
            date: this.task.date || '',
            error: ''
        };
    },
    computed: {
        todayDate() {
            const today = new Date();
            const yyyy = today.getFullYear();
            const mm = String(today.getMonth() + 1).padStart(2, '0');
            const dd = String(today.getDate()).padStart(2, '0');
            return `${yyyy}-${mm}-${dd}`;
        }
    },
    methods: {
        async editTask() {
            try {
                this.error = '';
                if (this.title === '' || this.description === '' || this.date === '') {
                    this.error = '–ë—É–¥—å –ª–∞—Å–∫–∞, –∑–∞–ø–æ–≤–Ω—ñ—Ç—å –≤—Å—ñ –ø–æ–ª—è.';
                    return;
                }
                await axios.put(`https://mytodolist-pcw4.onrender.com/tasks/${this.task.id}/`, {
                    title: this.title,
                    description: this.description,
                    completed: this.completed,
                    date: this.date,
                    user_id: this.task.user_id 
                });
                this.$emit('fetchTasks');
                this.$emit('editbutton', 0); 
            } catch (err) {
                if (err.response) {
                    if (err.response.status === 400 && err.response.data && err.response.data.detail === "Incorrect username") {
                        this.error = '–ö–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞ –∑ —Ç–∞–∫–∏–º email –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ';
                    } else {
                        this.error = `–ü–æ–º–∏–ª–∫–∞: ${err.response.data.detail || '–ù–µ –≤–¥–∞–ª–æ—Å—è —É–≤—ñ–π—Ç–∏. –°–ø—Ä–æ–±—É–π—Ç–µ –ø—ñ–∑–Ω—ñ—à–µ.'}`;
                    }
                } else if (err.request) {
                    this.error = '–ù–µ –≤–¥–∞–ª–æ—Å—è –ø—ñ–¥–∫–ª—é—á–∏—Ç–∏—Å—è –¥–æ —Å–µ—Ä–≤–µ—Ä–∞. –ü–µ—Ä–µ–≤—ñ—Ä—Ç–µ –≤–∞—à–µ —ñ–Ω—Ç–µ—Ä–Ω–µ—Ç-–∑\'—î–¥–Ω–∞–Ω–Ω—è.';
                } else {
                    this.error = '–°—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ –ø—ñ–¥ —á–∞—Å –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –∑–∞–ø–∏—Ç—É.';
                }
            }
        }
    }
}
</script>

